* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-size: 16px;
}

html,
body {
  height: 100%;
}

ul {
  list-style: none;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #808080;
  background: -webkit-linear-gradient(to right, #3fada8, #808080);
  background: -webkit-gradient(linear, left top, right top, from(#3fada8), to(#808080));
  background: -webkit-linear-gradient(left, #3fada8, #808080);
  background: -o-linear-gradient(left, #3fada8, #808080);
  background: linear-gradient(to right, #3fada8, #808080);
  color: #fff;
}

input[type='search'] {
  background-color: rgb(255, 255, 255, 0.5);
  -webkit-appearance: none;
  border: 1px solid transparent;
  border-radius: 4px;
  -webkit-box-shadow: none;
          box-shadow: none;
  font-size: 1rem;
  line-height: 1.5;
  vertical-align: top;
  padding: 1.2em 1.2em;
}

.weather,
.loading {
  margin-bottom: 2em;
  text-align: center;
}

.error {
  margin-bottom: 2em;
  text-align: center;
}

.temp {
  font-size: 4rem;
}

.loader {
  border: 2px solid #f3f3f3;
  border-top: 2px solid #43aba7;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
  margin: 50px auto;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



